import React from 'react';
import './App.css';
import Header from "./components/Header"



class App extends React.Component {
  constructor(props){
    super(props)

    this.state = {
      storeData: [],
      statsData: [],
      currentView: "home"
    }

    this.handleStoreClick = this.handleStoreClick.bind(this)
    this.handleStatsClick = this.handleStatsClick.bind(this)
  }

    
  handleStoreClick(){
    fetch("https://fortnite-public-api.theapinetwork.com/prod09/store/get")
    .then(function(response){
      return response.json()
    }).then(data => {
      
      console.log(data)
  
      const dataDone = data.items.map(item => {
        return (
            <a href={item.item.images.transparent} className={`item-card ` + item.item.rarity}>
              <img className="trans-img" src={item.item.images.transparent} />
              <h1 className="card-title">{item.name}</h1>
              <div className="cost-container">
                
                <img className="currency" src="https://fortnite-public-files.theapinetwork.com/fortnite-vbucks-icon.png" />
                <p className="cost">{item.cost}</p>
              </div>
            </a>
        )
      })

      this.setState({
        storeData: dataDone,
        currentView: "store"
      })
    })
  }

  handleStatsClick(){
    fetch("https://fortnite-public-api.theapinetwork.com/prod09/users/public/br_stats_v2?user_id=565eb4ecef6a4fd6b41881a90da5eb64")
    .then(function(response){
      return response.json()
    }).then(data => {
      
      console.log(data)


      this.setState(prevState => {
         return {
           statsData: [...prevState.statsData, data],
           currentView: "stats"
         }        
      })
      console.log(this.state.statsData)
          
     
    })
  }

  render() {
    return (
      <div className="App">
        <Header 
          handleStoreClick={this.handleStoreClick}
          handleStatsClick={this.handleStatsClick}
        />
        <div className="view-container">
          {this.state.currentView == "store" && this.state.storeData}
          {/* {this.state.currentView == "stats" && this.state.statsData} */}
          <p style={{color: "#fff"}} className="whitepee"></p>
        </div>
      </div>
    );
  }
}

export default App;
